body{
  font-family: 'Open Sans', sans-serif;
}
.Quiz-App {
  margin: 50px auto;
  width: 800px;
  background-color: #f8f8f8;
  padding: 15px;
}

.header-app{
  display: flex;
  background-color: #fff;
  padding: 0 20px;
  justify-content: space-between;
  align-items: center;
}
.header-app h4 {
  font-size: 17px;
}

.header-app h4 span {
  color: #b18040;
  font-size: 17px;
}

.header-app h2 span {
  color: #b18040;
 
}



.content-app {
  background-color: white;
  margin-top: 15px;
  text-align: center;
}

.quizArea h1 {
  padding: 15px;
  color: #b18040;
  line-height: 50px;
}
.content-app input {
  width: 76%;
  margin: 15px 0;
  padding: 15px;
  border: 2px solid #e9e9e9;
  text-align: center;
  font-size: 18px;
  border-radius: 8px;
}

.content-app input:focus {
  outline-color: #b18040;
}

.btn{
  font-size: 20px;
  width: 80%;
  padding: 15px 25px;
  color: white;
  background-color: #b18040;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  margin-bottom: 15px;
}




.footer-app{
  display: flex;
  justify-content: space-between;
  background-color: white;
  margin:15px ;
  padding: 15px;
  border-radius: 2px;
}
.footer-app .difficulty {
  font-size: 20px;
  font-weight: bold;
}
.footer-app .difficulty span {
  color:#b18040;
}
.footer-app .type {
  font-size: 20px;
  font-weight: bold;
}
.footer-app .type span {
  color: #b18040;
}

@media (min-width:350px) and (max-width:767px) {
  body{
    padding: 20px;
  }
  .Quiz-App {
    width: 95%;
  }
  .header-app{
    display: flex;
    flex-direction: column;
    align-items: start;
    padding: 0 10px;
    
  }
  .header-app h2 {
    font-size: 15px;
  }
  .header-app h4 {
    font-size: 15px;
    line-height: 25px;
  }
  
  .header-app h4 span {
    font-size: 15px;
  }
  .content-app {
    margin-top: 10px;
  }
  
  .quizArea h1 {
    padding: 10px;
    line-height: 40px;
  }
  .content-app input {
    width: 70%;
    margin: 10px 0;
    padding: 10px;
    font-size: 15px;
  }
  .btn{
    font-size: 16px;
    width: 76%;
    padding: 10px 18px;
    margin-bottom: 10px;
  }


  .footer-app{
    margin:10px ;
    padding: 10px;
  }
  .footer-app .difficulty {
    font-size: 16px;

  }
  .footer-app .type {
    font-size: 16px;
  }

}